import React, {useEffect, useState} from 'react'
import dayjs from 'dayjs'
import {Button, Card, DateRangePicker, Pagination, Select, Tooltip} from "element-react";
import {AiFillInfoCircle} from 'react-icons/ai'
import {FaListAlt} from 'react-icons/fa'
import {RiBarChartBoxFill} from 'react-icons/ri'

import {initDate, initCategoryOptions, initRegionOptions} from "../utils/common";
import HorizontalBarChart from "../components/HorizontalBarChart";
import VerticalDoughnutChart from "../components/VerticalDoughnutChart";
import {
    getTableTopBrand,
    getTopBrandBarChart,
    getTopBrandPieChart,
} from "../apis/market";
import {PieChartItem} from "../types/chart";
import {
    SearchParams, TableTopBrandResponse,
    TopBrandBarChart,
    TopBrandPieItem
} from "../types/market";
import {formatDate} from "../utils/common";
import {CHART_TYPE} from "../types/common";
import {ProductsParams, ProductsResponse} from "../types/product";
import {getProducts, getProductsDownloadUrl} from "../apis/product";
import ProductCards from "../components/ProductCards";
import TableChart from "../components/TableChart";

function formatData(data: TopBrandBarChart | null): [string[], { data: string, value: number }[]] {
    if (!data) return [[], []]
    const formatContent = data.items.content.sort((a: TopBrandPieItem, b: TopBrandPieItem) => b.count - a.count)
    const yAxisData = formatContent.map((it: TopBrandPieItem) => it.brand)
    const seriesData = formatContent.map((it: TopBrandPieItem) => ({data: it.brand, value: it.count}))
    return [yAxisData, seriesData]
}

function BrandRanking() {
    const [chartType, setChartType] = useState(CHART_TYPE.CHART)

    const [category, setCategory] = useState('webcam')
    const [region, setRegion] = useState('US')
    const [date, setDate] = useState<ElementReactLibs.dateType[]>(initDate())
    const [brand, setBrand] = useState<string | null>(null)

    const [page, setPage] = useState(1)
    const [productsPage, setProductsPage] = useState(1)

    const [brandProducts, setBrandProducts] = useState<TopBrandBarChart | null>(null)
    const [table, setTable] = useState<TableTopBrandResponse | null>(null)
    const [topBrandData, setTopBrandData] = useState<PieChartItem[]>([])
    const [products, setProducts] = useState<ProductsResponse | null>(null)

    const categoryOptions = initCategoryOptions()
    const regionOptions = initRegionOptions()

    const tableColumn: ElementReact.TableColumn[] = [
        {label: 'Brand', prop: 'brand'},
        {label: 'Products in Top 100', prop: 'count', sortable: true},
        {label: 'Sales Volume', prop: 'sales'},
        {label: 'Revenue', prop: 'revenue'},
        {label: 'Best Product Ranking', prop: 'rank'},
    ]

    useEffect(() => {
        const fetchData = async () => {
            const [start_at, end_at] = formatDate(date)
            const params: SearchParams = {
                category,
                region,
                start_at,
                end_at,
            }
            const {data: products} = await getTopBrandBarChart({...params, page})
            const {data: pie} = await getTopBrandPieChart(params)
            const pieChartItems = pie.items.map((it: TopBrandPieItem): PieChartItem => ({
                name: it.brand,
                value: it.count
            }))
            const {data: table} = await getTableTopBrand({...params, page});
            const brand = products.items.content[0]!.brand
            setBrand(brand)
            fetchProductData(brand)
            setTopBrandData(pieChartItems)
            setBrandProducts(products)
            setTable(table)
        }
        fetchData()
    }, [category, region, date, page])

    const fetchProductData = async (brand?: string) => {
        const [start_at, end_at] = formatDate(date)
        const params: ProductsParams = {category, region, page: productsPage, start_at, end_at}
        if (brand) params.brand = brand
        const {data} = await getProducts(params)
        setProducts(data)
    }

    useEffect(() => {
        setBrand(null)
    }, [category, region, date])

    useEffect(() => {
        fetchProductData(brand ? brand : undefined)
    }, [productsPage, brand])

    const onChartTypeChange = () => {
        if (chartType === CHART_TYPE.CHART) setChartType(CHART_TYPE.TABLE)
        if (chartType === CHART_TYPE.TABLE) setChartType(CHART_TYPE.CHART)
    }

    const handleDownloadProducts = async () => {
        const [start_at, end_at] = formatDate(date);
        const params: SearchParams = {
            category,
            region,
            start_at,
            end_at
        }
        const {request} = await getProductsDownloadUrl(params);
        window.open(request.responseURL, '__blank')
    }

    const handleDownloadTable = async () => {
        const [start_at, end_at] = formatDate(date);
        const params: SearchParams = {
            category,
            region,
            start_at,
            end_at,
            page,
            dl: 1,
        }
        const {request} = await getTableTopBrand(params);
        window.open(request.responseURL, '__blank')
    }

    return (
        <div>
            {/* Search Bar */}
            <div className="flex items-center mb-4">
                <Select className="mr-4" value={category} placeholder="Select"
                        onChange={(value: string) => setCategory(value)}>
                    {
                        categoryOptions.map(it => {
                            return <Select.Option key={it.value} label={it.label} value={it.value}/>
                        })
                    }
                </Select>
                <Select className="mr-4" value={region} placeholder="Select"
                        onChange={(value: string) => setRegion(value)}>
                    {
                        regionOptions.map(it => {
                            return <Select.Option key={it.value} label={it.label} value={it.value}/>
                        })
                    }
                </Select>
                <DateRangePicker
                    isReadOnly={true}
                    className="mr-4"
                    value={date}
                    disabledDate={(_date: Date) => dayjs(_date).day() !== 0}
                    onChange={setDate}
                />
                <Tooltip
                    className="mr-4"
                    effect="dark"
                    content={
                        <div style={{maxWidth: '200px'}}>
                            Sold Amount is estimated by multiplaying new reviews in each price range by 100.
                        </div>
                    }
                    placement="bottom"
                >
                    <AiFillInfoCircle className="text-2xl"/>
                </Tooltip>
                <div className="text-2xl cursor-pointer" onClick={onChartTypeChange}>
                    {
                        chartType === CHART_TYPE.CHART && <FaListAlt/>
                    }
                    {
                        chartType === CHART_TYPE.TABLE && <RiBarChartBoxFill/>
                    }
                </div>
            </div>

            <div className="flex">
                <div className="w-2/3 pr-2">
                    <Card className="mb-4" style={{minHeight: '642px'}}>
                        {
                            chartType === CHART_TYPE.CHART && brandProducts &&
                            <>
                              <HorizontalBarChart
                                title="Brand's Products in Top 100"
                                height="572px"
                                data={formatData(brandProducts)}
                                dataItemName="Products"
                                onClick={(value: string) => setBrand(value)}
                              />
                              <div className="flex justify-end">
                                <Pagination
                                  layout="prev, pager, next"
                                  pageCount={brandProducts.info.max_page}
                                  small={true}
                                  currentPage={brandProducts.items.current_page}
                                  onCurrentChange={(value: number) => setPage(value)}
                                />
                              </div>
                            </>
                        }
                        {
                            chartType === CHART_TYPE.TABLE && table &&
                            <>
                              <TableChart height="572px" columns={tableColumn} data={table.data.content}
                                          onDownload={handleDownloadTable}/>
                              <div className="flex justify-end">
                                <Pagination
                                  layout="prev, pager, next"
                                  pageCount={table.info.max_page}
                                  small={true}
                                  currentPage={table.data.current_page}
                                  onCurrentChange={(value: number) => setPage(value)}
                                />
                              </div>
                            </>
                        }
                    </Card>
                </div>

                <div className="w-1/3 pl-2">
                    <Card>
                        <VerticalDoughnutChart
                            title="Top 5 Brands -"
                            subtitle="Models in Top 100"
                            height="600px"
                            data={topBrandData}/>
                    </Card>
                </div>
            </div>

            {/* Product Cards */}
            <div className="mt-16">
                <div className="flex">
                    <p className="text-2xl mr-2">{brand}</p>
                    <Button type="primary" onClick={handleDownloadProducts}>Download</Button>
                </div>
                {
                    products &&
                    <ProductCards
                      data={products.prods}
                      region={region}
                      page={products.info.current_page}
                      totalPage={products.info.max_page}
                      onChange={setProductsPage}
                    />
                }
            </div>
        </div>
    )
}

export default BrandRanking
