import React, {useEffect, useState} from 'react'
import dayjs from 'dayjs'
import {Card, DateRangePicker, Select, Tooltip, Pagination, Button} from "element-react";
import {AiFillInfoCircle} from 'react-icons/ai'
import {FaListAlt} from 'react-icons/fa'
import {RiBarChartBoxFill} from 'react-icons/ri'

import Collapse from "../components/Collapse";
import ProductCards from "../components/ProductCards";
import {initDate, initCategoryOptions, initRegionOptions} from "../utils/common";
import VerticalProgressBarChart from "../components/VerticalProgressBarChart";
import {getTableTopProduct, getTopProductBarChart} from "../apis/market";
import {ProductBarChart, ProductBarItem, SearchParams, TableTopProductResponse} from "../types/market";
import {formatDate} from "../utils/common";
import {CHART_TYPE} from "../types/common";
import {ProductsParams, ProductsResponse} from "../types/product";
import {getProducts, getProductsDownloadUrl} from "../apis/product";
import TableChart from "../components/TableChart";

function formatTopBrandData(data: ProductBarChart): [string[], {name: string, data: string, value: number}[], number[]] {
    const formatContent = data.items.content.sort((a: ProductBarItem, b: ProductBarItem) => b.count - a.count)
    const yAxisData = formatContent.map((it: ProductBarItem) => it.name)
    const seriesData = formatContent.map((it: ProductBarItem) => ({name: it.name, data: it.ASIN, value: it.count}))
    const ranks = formatContent.map((it: ProductBarItem) => it.avg_rank)
    return [yAxisData, seriesData, ranks]
}

function ProductsRanking() {
    const [chartType, setChartType] = useState(CHART_TYPE.CHART)

    const [category, setCategory] = useState('webcam')
    const [region, setRegion] = useState('US')
    const [date, setDate] = useState<ElementReactLibs.dateType[]>(initDate())
    const [filter, setFilter] = useState<any>({});
    const [asin, setAsin] = useState<string | null>(null)

    const [page, setPage] = useState(1)
    const [productsPage, setProductsPage] = useState(1)

    const [rankings, setRankings] = useState<ProductBarChart | null>(null)
    const [table, setTable] = useState<TableTopProductResponse | null>(null)
    const [products, setProducts] = useState<ProductsResponse | null>(null)

    const categoryOptions = initCategoryOptions()
    const regionOptions = initRegionOptions()

    const tableColumn: ElementReact.TableColumn[] = [
        {label: 'Rank', prop: 'rank'},
        {label: 'Product', prop: 'name'},
        {label: 'Brand', prop: 'brand'},
        {label: 'ASIN', prop: 'ASIN'},
        {label: 'Sales', prop: 'sales', sortable: true},
        {label: 'Times in Top 30', prop: 'count'},
    ]

    useEffect(() => {
        const fetchData = async () => {
            const [start_at, end_at] = formatDate(date);
            const params: SearchParams = {
                category,
                region,
                start_at,
                end_at,
                page,
                ...filter
            }
            const {data} = await getTopProductBarChart(params)
            const {data: table} = await getTableTopProduct(params);
            setRankings(data)
            setTable(table)
        }
        fetchData()
    }, [category, region, date, filter, page])

    const fetchProductData = async () => {
        const [start_at, end_at] = formatDate(date)
        const params: ProductsParams = {category, region, page: productsPage, start_at, end_at, filter}
        if (asin) params.asin = asin
        const {data} = await getProducts(params)
        setProducts(data)
    }

    useEffect(() => {
        fetchProductData()
    }, [productsPage, asin])

    useEffect(() => {
        setAsin(null)
        fetchProductData()
    }, [category, region, date])

    const handleDownloadProducts = async () => {
        const [start_at, end_at] = formatDate(date);
        const params: SearchParams = {
            category,
            region,
            start_at,
            end_at,
            filter
        }
        const {request} = await getProductsDownloadUrl(params);
        window.open(request.responseURL, '__blank')
    }

    const handleDownloadTable = async () => {
        const [start_at, end_at] = formatDate(date);
        const params: SearchParams = {
            category,
            region,
            start_at,
            end_at,
            page,
            dl: 1,
            ...filter
        }
        const {request} = await getTableTopProduct(params);
        window.open(request.responseURL, '__blank')
    }

    const onChartTypeChange = () => {
        if (chartType === CHART_TYPE.CHART) setChartType(CHART_TYPE.TABLE)
        if (chartType === CHART_TYPE.TABLE) setChartType(CHART_TYPE.CHART)
    }

    return (
        <div>
            {/* Search Bar */}
            <div className="flex items-center mb-4">
                <Select className="mr-4" value={category} placeholder="Select"
                        onChange={(value: string) => setCategory((value))}>
                    {
                        categoryOptions.map(it => {
                            return <Select.Option key={it.value} label={it.label} value={it.value}/>
                        })
                    }
                </Select>
                <Select className="mr-4" value={region} placeholder="Select"
                        onChange={(value: string) => setRegion(value)}>
                    {
                        regionOptions.map(it => {
                            return <Select.Option key={it.value} label={it.label} value={it.value}/>
                        })
                    }
                </Select>
                <DateRangePicker
                    isReadOnly={true}
                    className="mr-4"
                    value={date}
                    disabledDate={(_date: Date) => dayjs(_date).day() !== 0}
                    onChange={setDate}
                />
                <Tooltip
                    className="mr-4"
                    effect="dark"
                    content={
                        <div style={{maxWidth: '200px'}}>
                            Sold Amount is estimated by multiplaying new reviews in each price range by 100.
                        </div>
                    }
                    placement="bottom"
                >
                    <AiFillInfoCircle className="text-2xl"/>
                </Tooltip>
                <div className="text-2xl cursor-pointer" onClick={onChartTypeChange}>
                    {
                        chartType === CHART_TYPE.CHART && <FaListAlt/>
                    }
                    {
                        chartType === CHART_TYPE.TABLE && <RiBarChartBoxFill/>
                    }
                </div>
            </div>

            <div className="flex">
                <div className="w-2/3 pr-2">
                    <Card className="mb-4" style={{minHeight: '642px'}}>
                        {
                            chartType === CHART_TYPE.CHART && rankings &&
                            <div>
                              <VerticalProgressBarChart
                                title="Product - No. of Times in Top 30"
                                height="572px"
                                data={formatTopBrandData(rankings)}
                                onClick={(value: string) => setAsin(value)}
                                padding="0 20px 0 50px"
                              />
                              <div className="flex justify-end">
                                <Pagination
                                  layout="prev, pager, next"
                                  pageCount={rankings.info.max_page}
                                  small={true}
                                  currentPage={rankings.items.current_page}
                                  onCurrentChange={(value: number) => setPage(value)}
                                />
                              </div>
                            </div>
                        }
                        {
                            chartType === CHART_TYPE.TABLE && table &&
                            <>
                              <TableChart height="572px" columns={tableColumn} data={table.items.content}
                                          onDownload={handleDownloadTable}/>
                              <div className="flex justify-end">
                                <Pagination
                                  layout="prev, pager, next"
                                  pageCount={table.info.max_page}
                                  small={true}
                                  currentPage={table.items.current_page}
                                  onCurrentChange={(value: number) => setPage(value)}
                                />
                              </div>
                            </>
                        }
                    </Card>
                </div>

                <div className="w-1/3 pl-2">
                    <Card>
                        <Collapse height="600px" type={category} onFilter={setFilter}/>
                    </Card>
                </div>
            </div>

            {/* Product Cards */}
            <div className="mt-16">
                <div className="flex">
                    {
                        asin
                            ? <p className="text-2xl mr-2">{asin}</p>
                            : <p className="text-2xl mr-2">Top 30 Products</p>
                    }
                    <Button type="primary" onClick={handleDownloadProducts}>Download</Button>
                </div>
                {
                    products &&
                    <ProductCards
                      data={products.prods}
                      region={region}
                      page={products.info.current_page}
                      totalPage={products.info.max_page}
                      onChange={setProductsPage}
                    />
                }
            </div>
        </div>
    )
}

export default ProductsRanking
