export const takeIntervalAxisSetting = (list: number[]): { max: number; min: number; interval: number } => {
    const SPLIT_NUMBER = 5
    const min = Math.floor(list.reduce((m, num) => m > num ? num : m, 0) * 0.8)
    const max = Math.ceil(list.reduce((m, num) => m < num ? num : m, 0) * 1.2)
    const interval = list ? (max - min) / SPLIT_NUMBER : 0
    return {min, max, interval}
}
